import React from 'react'

import { StateProvider, useStateContext, initialState, reducer } from './state'

interface Props {
  children?: React.ReactNode
}

const Sled: React.FC<Props> = (props) => {
  const { children } = props
  return (
    <StateProvider
      reducer={reducer}
      initialState={initialState}
    >
      {children}
    </StateProvider>
  )
}


export { Sled }
